.invalidInput {
    border: 1px solid red !important;
}
.filter_box {
    display: flex;
    div {
        display: flex;
        align-items: center;
        margin-right: 16px;
        input, select {
            width: 90px;
            margin-left: 4px;
        }
        span {
            font-size: 12px;
        }
    }
}
.table {
    width: 100%;
    border: 1px solid grey;
    padding: 16px;
}
input, select {
    margin: 0 16px;
    width: 220px;
}
.row {
    display: flex;
    width: 100%;
    margin-bottom: 4px;
}
.item {
    display: flex; 
    width: 50%
}
.label {
    width: 150px
}
.btn {
    margin-top: 20px;
    display: flex;
    justify-content: space-between;
}
button {
    background-color: black;
    color: white;
    padding: 4px 8px;
    border-radius: 2px;
    min-width: 80px;
    margin-right: 8px;
}
.register {
    display: flex;
    justify-content: space-between;
    margin-top: 16px;
    margin-bottom: 24px;
}