.filter_box {
    display: flex;
    div {
        display: flex;
        align-items: center;
        margin-right: 16px;
        input, select {
            width: 90px;
            margin-left: 4px;
        }
        span {
            font-size: 12px;
        }
    }
}
button {
    background-color: black;
    color: white;
    padding: 4px 8px;
    border-radius: 2px;
}
.register {
    display: flex;
    justify-content: flex-end;
    margin-top: 30px;
    margin-bottom: 16px;
}